.articles {
    position: relative;
	margin: 0 auto;
	padding: 0 20px;
    max-width: 980px;
}

.article_item {
    display: inline-flex;
    margin-bottom: 3rem;
}

.article_item:last-child {
    margin-bottom: 0;
}

.article_item img {
    width: 150px;
    height: 100px;
    min-width: 150px;
    min-height: 100px;
    background-color:var(--image-error-color);
    border-radius: 3px;
    margin-right: 20px;
    margin-top: 2.5rem;
}

@media only screen and (max-width:768px) {
    .article_item img {
        display: none;
    }
}

.article_date {
    color: var(--date-text-color);
    font-size: .8rem;
    letter-spacing: 2px;
    line-height: 1.5;
    margin-bottom: 1rem;
}

.article_title {
    font-size: 1.6rem;
    margin-bottom: 0.53571em;
    color: var(--title-text-color);
    margin: 0 0 0.75em;
    font-weight: bold;
    line-height: 2rem;
}

@media only screen and (max-width:768px) {
    .article_title {
        font-size: 1.5rem;   
    }
}

.article_tags {
	display: inline-flex;
    margin-right: 1rem;
}

.article_tag {
    font-size: 15px;
    color: var(--accent-color);
    font-weight: bold;
}

.article_tag:hover {
    color: var(--text-color);
}

.article_tag:before {
	content: "\0023";
	margin-right: 2px;
}

.article_tag:after {
    content: "、";
}

.article_tag:last-child:after {
    content: "";
}

