
.header {
	margin-top: 40px;
}

@media only screen and (min-width:768px) {
	.header {
		margin-top: 50px
	}
}

@media only screen and (min-width:1024px) {
	.header {
		margin-top: 60px
	}
}

@media only screen and (min-width:1220px) {
	.header {
		margin-top: 80px
	}
}

.wrap {
    position: relative;
	margin: 0 auto;
	padding: 0 20px;
    max-width: 870px;
}

.header__title {
	font-size: 25px;
	font-weight: 700;
	letter-spacing: -.02em;
	line-height: 1;
}

@media only screen and (min-width:768px) {
	.header__title {
		font-size: 30px
	}
}

@media only screen and (min-width:768px) {
	.header__title {
		font-size: 32px
	}
}

@media only screen and (min-width:1220px) {
	.header__title {
		font-size: 35px
	}
}


@media only screen and (min-width:1024px) {
	.menu {
		position: absolute;
		top: 50%;
		right: 20px;
		-moz-transform: translate(0,-50%);
		-o-transform: translate(0,-50%);
		-ms-transform: translate(0,-50%);
		-webkit-transform: translate(0,-50%);
		transform: translate(0,-50%)
	}
}

.menu__toggle {
	position: absolute;
	z-index: 10002;
	top: 50%;
	right: 20px;
	-moz-transform: translate(0,-50%);
	-o-transform: translate(0,-50%);
	-ms-transform: translate(0,-50%);
	-webkit-transform: translate(0,-50%);
	transform: translate(0,-50%);
	font-size: 15px;
	line-height: 20px;
	color: var(--title-text-color)
}

@media only screen and (min-width:1024px) {
	.menu__toggle {
		display: none
	}
}

.menu__toggle__icon {
	float: right;
	position: relative;
	width: 24px;
	height: 20px;
}

.menu__toggle__icon span,.menu__toggle__icon span:after,.menu__toggle__icon span:before {
	content: '';
	position: absolute;
	top: 8px;
	left: 0;
	width: 100%;
	height: 3px;
	-webkit-transition: all .1s;
	-moz-transition: all .1s;
	-ms-transition: all .1s;
	-o-transition: all .1s;
	transition: all .1s;
	background: var(--title-text-color)
}

.menu__toggle__icon span:before {
	top: -8px
}

.menu__toggle__icon span:after {
	top: 8px
}

.menu--open .menu__toggle__icon span:after,.menu--open .menu__toggle__icon span:before {
	background: #fff!important
}

.menu--open .menu__toggle__icon span {
	background-color: transparent!important
}

.menu--open .menu__toggle__icon span:after,.menu--open .menu__toggle__icon span:before {
	top: 0
}

.menu--open .menu__toggle__icon span:before {
	-moz-transform: rotate(45deg);
	-o-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg)
}

.menu--open .menu__toggle__icon span:after {
	-moz-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg)
}

.menu__wrap {
	position: fixed;
	z-index: 1000;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: rgba(242,120,75,.95);
	overflow: auto;
	visibility: hidden;
	opacity: 0;
	-webkit-transition: all .2s ease-in-out;
	-moz-transition: all .2s ease-in-out;
	-ms-transition: all .2s ease-in-out;
	-o-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out
}

@media only screen and (min-width:1024px) {
	.menu__wrap {
		position: relative;
		background: 0 0;
		visibility: visible;
		overflow: visible;
		opacity: 1;
		-webkit-transition: none;
		-moz-transition: none;
		-ms-transition: none;
		-o-transition: none;
		transition: none
	}
}

.menu--open .menu__wrap {
	visibility: visible;
	opacity: 1
}

.menu__list {
	list-style: none;
	margin: 0 auto;
	padding: 30px
}

@media only screen and (min-width:768px) {
	.menu__list {
		padding: 40px 0 0;
		max-width: 70%
	}
}

@media only screen and (min-width:1024px) {
	.menu__list {
		max-width: none;
		margin: 0;
		padding: 0
	}
}

@media only screen and (min-width:1024px) {
	.menu__list__item {
		display: inline-block;
		margin-left: 20px
	}

	.menu__list__item:first-child {
		margin-left: 0
	}
}

.menu__list__item__link {
	font-size: 30px;
	font-weight: 600;
	color: #fff;
	padding: 10px 0
}

.menu__list__item__link:hover {
	color: var(--title-text-color)
}

@media only screen and (min-width:1024px) {
	.menu__list__item__link {
		padding: 0 0 3px;
		font-size: 15px;
		color: var(--title-text-color);
		border-bottom: 2px solid transparent
	}

	.menu__list__item__link:hover {
		color: #f2784b
	}
}

@media only screen and (min-width:1220px) {
	.menu__list__item__link {
		font-size: 17px
	}
}

.menu__list__item__link.active-link {
	color: var(--title-text-color)
}

@media only screen and (min-width:1024px) {
	.menu__list__item__link.active-link {
		color: var(--title-text-color);
		border-color: #f2784b
	}
}

.menu__button {
	margin-top: 15px;
	padding: 15px 20px 16px;
	font-size: 13px
}

@media only screen and (min-width:1024px) {
	.menu__button {
		margin-top: 0;
		padding: 11px 20px 12px;
		font-size: 15px
	}
}
