body {
	margin: 0;
	background: var(--background-color);
	color: var(--text-color)
}

::selection {
	background: var(--selection-color);
	color: var(--selection-text-color)
}

a {
	text-decoration: none;
	color: var(--title-text-color);
	font-weight: 600;
}

a:hover {
	color: var(--accent-color)
}

img {
	display: block;
	max-width: 100%;
}

h1,h2,h3,h4,h5,h6 {
	color: var(--title-text-color)
}

h1 {
	font-size: 24px;
	font-weight: 700;
	line-height: 1.2;
	letter-spacing: -.02em
}

@media only screen and (min-width:768px) {
	h1 {
		font-size: 34px
	}
}

@media only screen and (min-width:1220px) {
	h1 {
		font-size: 44px
	}
}

h2 {
	margin-top: 40px;
	font-size: 20px;
	font-weight: 700;
	line-height: 1.2;
	letter-spacing: -.02em
}

@media only screen and (min-width:768px) {
	h2 {
		font-size: 28px
	}
}

@media only screen and (min-width:1220px) {
	h2 {
		font-size: 32px
	}
}

h3 {
	margin-top: 40px;
	font-size: 22px;
	font-weight: 600;
	line-height: 1.3;
	letter-spacing: -.02em
}

@media only screen and (min-width:768px) {
	h3 {
		font-size: 25px
	}
}

@media only screen and (min-width:1220px) {
	h3 {
		font-size: 30px
	}
}

h4 {
	margin-top: 25px;
	font-size: 17px;
	font-weight: 700;
	line-height: 1.6;
	letter-spacing: 0
}

@media only screen and (min-width:768px) {
	h4 {
		font-size: 22px
	}
}

@media only screen and (min-width:1220px) {
	h4 {
		font-size: 22px
	}
}

h5 {
	margin-top: 15px;
	font-size: 17px;
	font-weight: 700;
	line-height: 1.6;
	letter-spacing: 0
}

@media only screen and (min-width:768px) {
	h5 {
		font-size: 20px
	}
}

@media only screen and (min-width:1220px) {
	h5 {
		font-size: 20px
	}
}

h6 {
	margin-top: 15px;
	font-size: 17px;
	font-weight: 700;
	line-height: 1.6;
	letter-spacing: 0
}

@media only screen and (min-width:768px) {
	h6 {
		font-size: 20px
	}
}

@media only screen and (min-width:1220px) {
	h6 {
		font-size: 20px
	}
}

ol,p,ul,summary {
	margin-top: 15px;
	font-size: 15px;
	font-weight: 400;
	line-height: 1.6;
	letter-spacing: 0
}

@media only screen and (min-width:768px) {
	ol,p,ul,summary {
		font-size: 16px
	}
}

@media only screen and (min-width:1220px) {
	ol,p,ul,summary {
		font-size: 18px
	}
}

p a {
	font-weight: 400;
	border-bottom: 2px solid #f2784b
}


// 引用块
blockquote {
	border-left: 4px solid #2a2f36;
	padding-left: 1rem;
	margin-left: 5px;
	margin-right: 0;
	font-style: italic;
	padding: .2rem 1rem;

	> p:first-of-type {
	  margin-top: 0;
	}
	> p:last-of-type {
	  margin-bottom: 0;
	}
}

// 代码
.highlighter-rouge {
	margin: 20px auto;
}

@media only screen and (min-width:768px) {
	.highlighter-rouge {
		margin: 30px auto;
	}
}

@media only screen and (min-width:1220px) {
	.highlighter-rouge {
		margin: 40px auto;
	}
}

.highlight {
	display: block;
	line-height: 1.5;
	font-size: 13px;
	overflow: auto
}

@media only screen and (min-width:768px) {
	.highlight {
		font-size: 14px
	}
}

@media only screen and (min-width:1220px) {
	.highlight {
		font-size: 16px
	}
}

.highlight .highlight {
	padding: 20px;
	margin: 0;
}

@media only screen and (min-width:768px) {
	.highlight .highlight {
		padding: 30px
	}
}


// 表格
table {
	width: 100%;
	border: 2px solid #2a2f36
}

table thead {
	background: #2a2f36;
	color: #fff;
	text-align: left;
	font-weight: 600
}

table thead th {
	font-weight: 600
}

table td, table th {
	padding: 10px;
	font-size: 15px;
	font-weight: 400
}

@media only screen and (min-width:768px) {
	table td, table th {
		padding: 15px;
		font-size: 17px
	}
}

@media only screen and (min-width:1220px) {
	table td, table th {
		padding: 20px;
		font-size: 19px
	}
}

 table tr:nth-child(2n) {
	background: #f4f5f6
}


// 注脚
.footnotes {
	margin-top: 50px
}

.footnotes ol {
	list-style: none;
	padding-left: 0;
	counter-reset: footnotes
}

.footnotes ol li {
	margin-top: 5px;
	font-size: 13px;
	counter-increment: footnotes
}

.footnotes ol li:before {
	content: counter(footnotes);
	box-sizing: border-box;
	display: inline-block;
	margin-right: 3px;
	width: 20px;
	height: 20px;
	border-radius: 100%;
	border-bottom: none;
	padding: 2px 3px;
	background: #f4f5f6;
	font-size: 11px;
	font-weight: 600;
	text-align: center;
	color: var(--second-text-color)
}

.footnotes ol li p {
	display: inline;
	max-width: 100%;
	font-size: 13px
}

.footnotes ol li p a.reversefootnote {
	border-bottom: 0;
	vertical-align: sub
}

sup a.footnote {
	display: inline-block;
	margin: 0 5px;
	height: 16px;
	width: 16px;
	border-radius: 100%;
	border-bottom: none;
	padding-bottom: 3px;
	background: #f4f5f6;
	font-size: 10px;
	text-align: center;
	color: var(--second-text-color)
}

sup a.footnote:hover {
	background: var(--second-text-color);
	color: #fff
}
