
.footer {
	margin: 135px 0 40px;
}

.footer__title {
	font-size: 26px;
	font-weight: 700;
	letter-spacing: -.02em;
	line-height: 1;
	color: var(--text-color)
}

.footer__title:hover {
	color: var(--second-text-color)
}

.footer__text {
	color: var(--second-text-color)
}

.footer__copyright {
	display: block;
	font-size: 13px;
	color: var(--second-text-color)
}

.footer .socials {
	margin-top: 25px;
}

@media only screen and (min-width:760px) {
	.footer .socials {
		position: absolute;
		top: 0;
		right: 20px;
		margin-top: 0
	}
}

.socials {
	list-style: none;
	margin: 0;
	padding: 0
}

.socials__item {
	display: inline-block;
	margin-left: 10px
}

.socials__item:first-child {
	margin-left: 0
}

.socials__item__link {
	position: relative;
	display: block;
	width: 36px;
	height: 36px;
	border-radius: 100%;
	background: var(--second-text-color);
	font-size: 17px;
	line-height: 1;
	color: #fff
}

.socials__item__link svg {
	position: absolute;
	top: 50%;
	left: 50%;
	-moz-transform: translate(-50%,-50%);
	-o-transform: translate(-50%,-50%);
	-ms-transform: translate(-50%,-50%);
	-webkit-transform: translate(-50%,-50%);
	transform: translate(-50%,-50%)
}

.socials__item__link:hover {
	background: var(--accent-color);
	color: #fff
}