.page__title {
	margin: 40px 0;
}

@media only screen and (min-width:768px) {
	.page__title {
		margin: 50px 0;
	}
}

@media only screen and (min-width:1024px) {
	.page__title {
		margin: 60px 0;
	}
}

@media only screen and (min-width:1220px) {
	.page__title {
		margin: 80px 0;
	}
}

.page__title p {
	margin-top: -10px;
}


.image-wrap {
	position: relative;
	max-width: 1180px;
	padding-left: 20px;
	padding-right: 20px;
	margin: 20px auto
}

@media only screen and (min-width:768px) {
	.image-wrap {
		margin: 30px auto
	}
}

@media only screen and (min-width:1220px) {
	.image-wrap {
		margin: 40px auto
	}
}

.image-wrap:first-child {
	margin-top: 0
}

.image-wrap img {
    margin: 0 auto;
    width: 100%;
}
