@import "style.scss";
@import "header.scss";
@import "articles.scss";
@import "page.scss";
@import "post.scss";
@import "footer.scss";
@import "highlighting.scss";

:root {
    --background-color: #fff;
    --title-text-color: #2a2f36;
    --text-color: #6c7a89;
    --second-text-color: #abb7b7;
    --date-text-color: #a1a1a7;
    --accent-color: #f2784b;
    --selection-color: #f2784b;
    --selection-text-color: #fff;
    --image-error-color: #f8f8f8;
}

@media (prefers-color-scheme: dark) {
    :root {
        --background-color: #333333;
        --title-text-color: #efefef;
        --text-color: #b8b8b8;
        --second-text-color: #666666;
        --date-text-color: #a1a1a7;
        --accent-color: #f2784b; 
        --selection-color: #f2784b;
        --selection-text-color: #e2e1e1;
        --image-error-color: #666666;
    }
    @import "highlighting_dark.scss";
}